import React from "react";
import Image from "next/image";
import home_page_1 from "@/public/images/home_page_1.svg";
import home_page_4 from "@/public/images/home_page_4.svg";
const Firstpage = () => {
  return (
    <div className="relative overflow-hidden min-h-screen">
      {/* Ana arka plan */}
      <Image
        src={home_page_1}
        alt="background"
        layout="fill"
        className="object-cover z-0"
        priority // تحميل الصورة بسرعة لأنها خلفية
      />
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 ">
        <div className="relative flex flex-wrap items-center justify-between">
          <div className="z-10">
            <Image
              src={home_page_4}
              alt="left image"
              layout="intrinsic"
              width={200}
              height={200}
              className="object-contain"
            />
            <Image
              src="/images/home_page_4.svg" // Path to the image
              alt="left image"
              width={500} // The desired width of the image
              height={300} // The desired height of the image
              className="object-contain"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Firstpage;
